:root {
  --black-color: #000;
  --white-color: #fff;
  --bg-color: #ae1332;
  --text-shadow: 0 4px 70px #0000001a;
  --letter-spacing: -.04em;
  --letter-spacing-smaller: -.02em;
}

body {
  color: var(--white-color);
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  color: currentColor;
  font-family: inherit;
  line-height: inherit;
  border: none;
}

input, textarea {
  font-family: inherit;
  line-height: inherit;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

.is-closed {
  display: none;
}

.is-hidden {
  visibility: hidden;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.header {
  max-width: 100%;
}

.header .nav-layout {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
  display: flex;
}

.nav-layout .site-nav {
  list-style-type: none;
  display: none;
}

.nav-layout .logo-box {
  gap: 8px;
  display: flex;
}

.nav-layout .logo-text {
  text-transform: uppercase;
  color: var(--bg-color);
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

.nav-layout .icon-basket {
  display: none;
}

.off-screen-menu {
  background-color: var(--bg-color);
  height: 100vh;
  width: 100%;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 50px;
  right: -1280px;
}

.off-screen-menu.active {
  right: 0;
}

.ham-menu {
  height: 24px;
  width: 20px;
  cursor: pointer;
  position: relative;
}

.ham-menu span {
  height: 2px;
  width: 100%;
  background-color: var(--bg-color);
  border-radius: 25px;
  transition: all .3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.ham-menu span:nth-child(1) {
  width: 50%;
  top: 25%;
  left: 5px;
}

.ham-menu span:nth-child(3) {
  width: 75%;
  top: 75%;
  left: 7.5px;
}

.ham-menu.active span:nth-child(1) {
  width: 100%;
  top: 50%;
  transform: translate(-50%, -50%)rotate(45deg);
}

.ham-menu.active span:nth-child(2) {
  opacity: 0;
}

.ham-menu.active span:nth-child(3) {
  width: 100%;
  top: 50%;
  transform: translate(-60%, -50%)rotate(-45deg);
}

.site-nav {
  display: flex;
}

.off-screen-menu .link {
  letter-spacing: var(--letter-spacing);
  text-align: center;
  color: var(--color-white);
  line-height: 129%;
}

.logo-text {
  text-transform: uppercase;
  text-align: center;
  color: var(--color-red);
  align-items: center;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

@media screen and (max-width: 767px) {
  .nav-layout .icon-basket {
    display: none;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1279px) {
  .header {
    max-width: 1279px;
    padding: 0 32px;
  }

  .nav-layout .ham-menu, .nav-layout .off-screen-menu {
    display: none;
  }

  .header .nav-layout {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .header .nav-layout .site-nav {
    gap: 16px;
    display: flex;
  }

  .nav-layout .site-nav .link {
    text-align: center;
    color: var(--bg-color);
    font-size: 14px;
    font-weight: 400;
    line-height: 2px;
  }

  .nav-layout .icon-basket {
    display: flex;
  }
}

@media only screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }

  .nav-layout .off-screen-menu, .nav-layout .ham-menu {
    display: none;
  }

  .header .nav-layout {
    justify-content: space-between;
    margin-left: 0;
  }

  .nav-layout .site-nav {
    justify-content: flex-start;
    margin-left: 32px;
    display: flex;
  }

  .nav-layout .site-nav .link {
    padding: 0 20px;
  }

  .nav-layout .logo-box {
    justify-content: center;
  }

  .nav-layout .icon-basket {
    margin-right: 42px;
